import React, { useState } from 'react';
import { Button, Container } from 'reactstrap';
import { DeleteConfirmation } from '../components/DeleteConfirmationModal';
import 'bootstrap/dist/css/bootstrap.min.css';

export default {
  /* 👇 The title prop is optional.
   * See https://storybook.js.org/docs/react/configure/overview#configure-story-loading
   * to learn how to generate automatic titles
   */
  title: 'People Analytics story',
  component: DeleteConfirmation,
};

export const DeleteConfirmationStory = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [message, setMessage] = useState('Are you sure you want to delete');
  return (
    <Container>
      <DeleteConfirmation
        isOpen={isOpen}
        toggle={e => setIsOpen(!isOpen)}
        message={message}
        handleConfirm={e => alert('Deleted ')}
      ></DeleteConfirmation>
      <Button color="danger" onClick={e => setIsOpen(!isOpen)}>
        Delete
      </Button>
    </Container>
  );
};



