import { Bar, Chart } from '../Chart/Chart';
import { Container, Row } from 'reactstrap';

export * from './Barchart';
// import { Chart, Bar } from './Chart';

export const Barchart = ({ data, y = 0, diff }) => {
  // Width of each bar
  const itemWidth = 20;

  //   // Distance between each bar
  const itemMargin = 5;

  const dataLength = data.length;

  //   // Normalize data, we'll reduce all sizes to 25% of their original value
  const massagedData = data.map(datum =>
    Object.assign({}, datum, { past: datum.past * 0.5, actual: datum.actual * 0.5 }),
  );

  const mostRepos = massagedData.reduce((acc, cur) => {
    const { past } = cur;
    return past > acc ? past : acc;
  }, 0);

  const chartHeight = mostRepos;

  if (!diff)
    return (
      <div>
        <Chart width={2 * dataLength * (itemWidth + itemMargin)} height={chartHeight}>
          {massagedData.map((datum, index) => (
            <>
              <Bar
                key={datum.name}
                x={
                  index == 0
                    ? index * (itemWidth + itemMargin)
                    : index * 2 * (itemWidth + itemMargin)
                }
                y={y}
                width={itemWidth}
                height={datum.past}
                fill="teal"
              />
              <Bar
                key={datum.name}
                x={
                  index == 0
                    ? (index + 0.85) * (itemWidth + itemMargin)
                    : (index * 2 + 0.85) * (itemWidth + itemMargin)
                }
                y={y}
                width={itemWidth}
                height={datum.actual}
                fill="orange"
              />
            </>
          ))}
        </Chart>
      </div>
    );
  else {
    return (
      <div>
        <Chart width={2 * dataLength * (itemWidth + itemMargin)} height={chartHeight}>
          {massagedData.map((datum, index) => (
            <Bar
              key={datum.name}
              x={
                index == 0 ? index * (itemWidth + itemMargin) : index * 2 * (itemWidth + itemMargin)
              }
              y={datum.actual - datum.past > 0 ? y : y + (datum.actual - datum.past)}
              width={itemWidth}
              height={Math.abs(datum.actual - datum.past)}
              fill={datum.actual - datum.past > 0 ? 'teal' : 'red'}
            />
          ))}
        </Chart>
      </div>
    );
  }
};
